nav {
  color: white;
  padding: 10px;
  text-align: center;
}
nav a {
  text-decoration: none;  
  color: black;
  padding: 10px;
}

.login-container, .register-container, .new-poem-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}

h2 {
  font-size: 2rem;
  margin-bottom: 1rem;
}

form {
  display: flex;
  flex-direction: column;
  width: 300px; /* Adjust width as needed */
  background-color: #fff;
  padding: 1rem;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

label {
  margin-bottom: 0.5rem;
  font-weight: bold;
}

input[type="text"],
input[type="email"],
input[type="password"] {
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 3px;
  margin-bottom: 1rem;
  font-size: 1rem;
}

.error {
  color: red;
  font-size: 1rem;
  margin-bottom: 1rem;
}

button {
  background-color: #007bff;
  color: #fff;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
}

button:hover {
  background-color: #0062cc;
}

.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #f5f5f5; /* Optional background color */
  padding: 2rem;
}

h1 {
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 1rem;
}

p {
  font-size: 1.2rem;
  line-height: 1.5;
  text-align: center;
}

/* Poem Card */
.poem-card-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); /* Adjust minmax values for desired size and responsiveness */
  gap: 1rem; /* Adjust gap for spacing between cards */
}

.poem-card {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 1rem;
  margin-bottom: 1rem; /* Remove if spacing is handled by grid gap */
}

.poem-card p:first-of-type {
  font-size: 1.2rem;
  line-height: 1.5;
  margin-bottom: 0.5rem;
}

.poem-card p:last-of-type {
  font-size: 0.8rem;
  color: #888;
  margin: 0;
}

.poem-card p:last-of-type span {
  font-weight: bold;
}

/* Other existing styles you might have */
h1 {
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 1rem;
}

p {
  font-size: 1.2rem;
  line-height: 1.5;
  text-align: center;
}

.dashboard h1 {
  text-align: center;
}
/* user details */

.user-details, .user-details p {
  text-align: left;
}